import { toURLSearchParams } from '@wix/sdk-runtime/rest-modules';
import { resolveUrl } from '@wix/sdk-runtime/rest-modules';
import { ResolveUrlOpts } from '@wix/sdk-runtime/rest-modules';
import { RequestOptionsFactory } from '@wix/sdk-types';

function resolveWixStoresCatalogProvisionV1StoresProvisionServiceUrl(
  opts: Omit<ResolveUrlOpts, 'domainToMappings'>
) {
  const domainToMappings = {
    'www.wixapis.com': [
      {
        srcPath: '/stores/v3/provision/version',
        destPath: '/v3/provision/version',
      },
    ],
    'manage._base_domain_': [
      {
        srcPath: '/stores/v3/provision/version',
        destPath: '/v3/provision/version',
      },
    ],
    'editor._base_domain_': [
      {
        srcPath: '/_api/provision/version',
        destPath: '/v3/provision/version',
      },
    ],
    'blocks._base_domain_': [
      {
        srcPath: '/_api/provision/version',
        destPath: '/v3/provision/version',
      },
    ],
    'create.editorx': [
      {
        srcPath: '/_api/provision/version',
        destPath: '/v3/provision/version',
      },
    ],
    '*.dev.wix-code.com': [
      {
        srcPath: '/_api/provision/version',
        destPath: '/v3/provision/version',
      },
      {
        srcPath: '/v3/provision/version',
        destPath: '/v3/provision/version',
      },
    ],
    _: [
      {
        srcPath: '/_api/stores/v3/provision/version',
        destPath: '/v3/provision/version',
      },
    ],
  };

  return resolveUrl(Object.assign(opts, { domainToMappings }));
}

const PACKAGE_NAME = '@wix/auto_sdk_stores_catalog-versioning';

/** Retrieves the version of Stores Catalog installed on a site. */
export function getCatalogVersion(payload: object): RequestOptionsFactory<any> {
  function __getCatalogVersion({ host }: any) {
    const metadata = {
      entityFqdn: 'wix.stores.catalog.v3.provision',
      method: 'GET' as any,
      methodFqn:
        'wix.stores.catalog.provision.v1.StoresProvisionService.GetCatalogVersion',
      packageName: PACKAGE_NAME,
      url: resolveWixStoresCatalogProvisionV1StoresProvisionServiceUrl({
        protoPath: '/v3/provision/version',
        data: payload,
        host,
      }),
      params: toURLSearchParams(payload),
    };

    return metadata;
  }

  return __getCatalogVersion;
}
